.page-home-container{
  display: flex;
  margin-top: 10px;
}
.page-left{
  width: 66.66666667%;
}
/* css create-post */
.create-post{
  background: white;
  border: 0.5px solid rgba(0,0,0,0.1);
  border-radius: 2px;
  position: relative;
}
.create-post-avatar{
  width: 50px;
  height: 50px;
  min-width: 50px;
  min-height: 50px;
  overflow: hidden;
  border-radius: 50%;
  border: 0.5px solid rgba(0,0,0,0.1);
}
.create-post-avatar img{
  min-width: 50px;
  min-height: 50px;
  object-fit: cover;
}
.create-post-input{
  display: flex;
  padding: 10px;
  border-bottom: 0.5px solid rgba(0,0,0,0.1);
}
.create-post-choose-image{
  display: flex;
}
.create-post-choose-image label, .create-post-tag{
  background: #f5f6f7;
  border-radius: 30px;
  border: 0.1px solid rgba(0,0,0,0.1);
  padding: 5px 20px;
  font-weight: 600;
  font-size: 14px;
  display: flex;
  cursor: pointer;
}
.create-post-choose-image input{
  visibility: hidden;
  opacity: 0;
}
.create-post-option{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}
.public-DraftEditorPlaceholder-root{
  margin-bottom: -20px;
  color: rgba(0,0,0,0.5);
  z-index: 1;
  position: relative;
}
.DraftEditor-editorContainer{
  z-index: 2;
  position: relative;
}
.create-post-choose-place-post{
  border-bottom: 0.2px solid rgba(0, 0, 0,0.1);
}
.ant-switch-checked{
  background: rgba(0,128,128) !important;
}

.create-post-submit{
  text-align: center;
  padding: 5px 0;
  margin-top: 5px;
}
.create-post-submit button{
  width: 90%;
  border: 0.2px solid rgba(0,0,0,0.1);
  background: rgba(0,128,128) !important;
  color: white !important;
  font-size: 16px;
  font-weight: 600;
  padding: 3px;
  border-radius: 3px;
}
.create-post-submit button:hover{
  background: rgba(0,128,128);
  color: white;
}
.create-post-newfeed-place, .create-post-story-place{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}
.place-post{
  margin-left: 10px;
}
.create-post-choose-place-post{
  padding: 10px;
}
.create-post-reaction{
  position: relative;
}
.create-post-reaction .list-emoji{
  width: 280px;
  height: 320px;
  background: white;
}
.icon-choose-emoji-picker{
  background: none;
  border: 0;
}
.icon-choose-emoji-picker:focus svg{
  fill: rgba(0,128,128) !important;
}
.ant-tooltip-inner{
  padding: 0 !important;
  background: none !important;
}
button:disabled{
  background: rgba(0,128,128,0.7) !important;
}
.create-post-input-tag{
  padding: 0 10px;
}
.creat-post-tag-list{
  margin: 10px;
  font-size: 14px;
}
.creat-post-tag-list a{
  color: rgba(0,128,128);
}
.create-post-more-mention{
  margin-bottom: 0;
  padding: 5px;
  background: rgba(0,0,0,0.7);
  list-style: none;
  border-radius: 5px;
  font-size: 14px;
}
.create-post-more-mention li a{
  color: white;
}
.create-post-more-mention li a:hover, .create-post-more-mention li:hover a{
  color: white;
}
.create-post-mention:hover{
  text-decoration: underline;
  cursor: pointer;
}
/* css create-post */
.page-right{
  width: 100%;
  margin-left: 10px;
}
.page-introduce, .page-create-at, .page-gallery{
  background: white;
  border-radius: 2px;
  padding: 10px;
  border: 1px solid rgba(0,0,0,0.1);
}
.page-create-at, .page-gallery{
  margin: 10px 0;
}
.page-introduce h5, .page-create-at h5, .page-gallery h5{
  text-align: center;
  font-size: 20px;
  font-weight: 600;
}
.page-create-at p{
  font-size: 14px;
  padding: 3px 0;
}
.page-introduce ul li span{
  color: rgba(0,128,128);
  cursor: pointer;
}
.page-introduce ul li span:hover{
  text-decoration: underline;
}
.page-introduce ul li{
  list-style: none;
  font-size: 14px;
  padding: 3px 0;
}
.page-introduce button:hover{
  background: rgba(0,128,128);
}
.page-introduce button{
  width: 100%;
  border: 1px solid rgba(0,0,0,0.1);
  border-radius: 2px;
  background: rgba(0,128,128, 0.8);
  color: white;
  font-size: 14px;
  font-weight: 600;
  padding: 3px 0;
  transition: all 0.2s ease;
}
.page-introduce-icon svg{
  font-size: 20px;
  fill: rgba(0,128,128);
  stroke:rgba(0,128,128);
}
.page-introduce-icon{
  margin-right: 10px;
}
.page-introduce-not-popup{
  color: black !important;
  text-decoration: none !important;
}
.page-create-time svg{
  font-size: 20px;
  fill: rgba(0,128,128);
}
.page-create-time span{
  margin-left: 10px;
  font-weight: 600;
  font-size: 14px;
}
.page-gallery h5{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.page-gallery-more{
  font-size: 14px;
}
.page-gallery-more a{
  color: rgba(0, 128, 128);
}
.page-gallery-more a:hover{
  color: rgba(0, 128, 128);
}
.page-gallery-img{
  overflow: hidden;
  border: 2px solid white;
  cursor: pointer;
}
.page-gallery-img img{
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.img-top{
  height: 100px;
}
.img-bottom{
  height: 80px;
}
.page-create-at-time-line{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: rgba(0,128,128,0.6);
  font-weight: 600;
}
